import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import DSAInfoBox from './controls/DSAInfoBox';
import { DSAGrid, DSAGridRow} from './controls/DSAGrid';


const styles = {
  root: {
    flexGrow: 1,
  }
};

const SadMain = (props) => {
  const { classes } = props;
  return <main className={classes.root}>
      <DSAGrid>
        <DSAGridRow>
          <DSAInfoBox title="Trauriges Ende der DSA Web Tools" text=" Ulisses hat mich angeschrieben und leider die DSA Web Tools für nicht zulässig erklärt: Material, das im Scriptorium Aventuris veröffentlicht wurde, muss dort exklusiv zu finden sein und darf nicht öffentlich ausserhalb des Portals zugänglich gemacht werden. Es hat mir Spaß gemacht das zu Entwickeln." />
        </DSAGridRow>
      </DSAGrid>
    </main>;
};

SadMain.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(SadMain);
